import $mAssetsPath from '@/config/assets.config';

export default {
		name: 'rfImage',
		props: {
			src: {
				type: String,
				default: $mAssetsPath.errorImage
			},
			// 显示模式
			mode: {
				type: String,
				default: 'aspectFill'
			},
			// 是否预览
			isPreviewImage: {
				type: Boolean,
				default: true
			}
		},
    // data() {
		// 	return {
    //     errorImage: this.
    //   };
    // },
		methods: {
			// 图片异常处理
			onImageError() {
				this.src = $mAssetsPath.errorImage;
			},
			// 图片预览
			previewImage(urls) {
				if (!this.isPreviewImage) return;
				if (!urls) return;
				uni.previewImage({
					urls: urls.split('#$#')
				});
			}
		}
	}
